import Component from './../component';

class Popup extends Component {
  constructor(data) {
    super();

    this._src = data.src;
    this._desctiption = data.desctiption;

    this._onCloseButtonClick = this._onCloseButtonClick.bind(this);
  }

  _onCloseButtonClick(evt) {
    evt.preventDefault();

    this._element.remove();
  }

  get template() {
    return `
      <div class="popup">
        <div class="popup__pic-wrapper">
          <img class="popup__pic" src="${this._src}" alt="сертификат ${this._desctiption}">
        </div>
        <button class="popup__close" aria-label="Закрыть попап" type="button"></button>
      </div>`
  }

  bind() {
    this._element.querySelector(`.popup__close`)
      .addEventListener(`click`, this._onCloseButtonClick);
  }

  unbind() {
    this._element.querySelector(`.popup__close`)
      .removeEventListener(`click`, this._onCloseButtonClick);
  }
}

export default Popup;
