import {sertificates} from './data/data';
import Popup from './components/popup';

const menuToggle = document.querySelector('.menu-toggle');
const menu = document.querySelector('.menu__wrapper');

menu.querySelectorAll('.menu__link').forEach((it) => {
  it.addEventListener('click', () => {
    if (menu.classList.contains('menu--opened')) {
      menuToggle.classList.remove('menu-toggle--opened');
      menu.classList.remove('menu--opened');
    }
  });
});

menuToggle.addEventListener('click', (evt) => {
  evt.preventDefault();
  menuToggle.classList.toggle('menu-toggle--opened');
  menu.classList.toggle('menu--opened');
})

// projects
const prodjectBlocks = document.querySelectorAll('.projects__view');
let selectedProject = null;
prodjectBlocks.forEach((it) => {
  it.addEventListener('click', (evt) => {
    if (selectedProject) {
      selectedProject.classList.remove('projects__view--current');
    }
    evt.preventDefault();
    it.classList.toggle('projects__view--current');
    selectedProject = it;
  })
});

const certificateLink = document.querySelectorAll('.education__list .button');
certificateLink.forEach((it, i) => {
  it.addEventListener('click', (evt) => {
    evt.preventDefault();
    document.querySelector(`body`)
      .appendChild(new Popup(sertificates[i]).render());
  })
});
